.weatherLogo {
    width: 140px;
    height: 140px;
    margin: 40px auto;
}

.chooseCityLabel {
    color: black;
    font-size: 18px;
    font-weight: bold;
    margin: 10px auto;
}

.searchBox {
    display: flex;
    flex-direction: row;
    border: black solid 1px;
    border-radius: 2px;
    color: black;
    font-weight: bold;
    margin: 20px auto;
}

.searchBox input {
    padding: 10px;
    font-size: 14px;
    border: none;
    outline: none;
    font-weight: bold;
}

.searchBox button {
    background-color: black;
    color: #eee;
    padding: 10px;
    border: 1px solid black;
    font-size: 14px;
    outline: none;
    font-weight: bold;
    cursor: pointer;
}